// React Imports
import React, { useState, useEffect }   from "react";
import MailchimpSubscribe               from "react-mailchimp-subscribe";
import InputField                       from './InputField';

// Style Imports
import "../assets/scss/index.scss";

const CustomMailchimpForm = ({ status, message, onValidated }) => {

    const [email, setEmail] = useState('');

    useEffect(() => {
        if (status === "success") clearFields();
    }, [status])

    const clearFields = () => {
        setEmail('');
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        email &&
            email.indexOf("@") > -1 &&
            onValidated({
                EMAIL: email,
            });
    }

    return (
        <form
            onSubmit={(e) => handleSubmit(e)}
        >
            {status != "sending" && status != "error" && status != "success" && (
                <h1>
                    a creative studio
                </h1>
            )}
            {status === "sending" && (
                <h1>
                    sending...
                </h1>
            )}
            {status === "error" && (
                <h1
                    dangerouslySetInnerHTML={{ __html: message }}
                />
            )}
            {status === "success" && (
                <h1>
                    thank you :)
                </h1>
            )}

            <div className="osh-field-container">
                <InputField
                    onChangeHandler={setEmail}
                    type="email"
                    value={email}
                    placeholder="enter email for updates"
                    isRequired
                />
            </div>

            <InputField
                type="submit"
                formValues={[email]}
            />
        </form>
    );
};

const ContactForm = props => {

    const postUrl = `https://studio.us13.list-manage.com/subscribe/post?u=b6e6e8aafcf46407b042fba60&id=902ad9814c`;

    return (
        <MailchimpSubscribe
            url={postUrl}
            render={({ subscribe, status, message }) => (
                <CustomMailchimpForm
                    status={status}
                    message={message}
                    onValidated={formData => subscribe(formData)}
                />
            )}
        />
    );
};

